.Task {
    background: #d8d8d8;
    margin: 10px 5px;
    padding: 10px 20px;
    cursor: pointer;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    line-height: 2.25em;
}

.Task.done {
    border-left: 5px solid #0DBC79;
}

.Task:active {
    transform: scale(0.98);
}

.row {
    display: flex;
    justify-content: space-between;
    align-items: baseline;
}

.Task h3 {
    font-weight: 400;
    font-size: 1.2em;
}

.Task.done h3 {
    font-style: italic;
    text-decoration: line-through;
}

.Task p {
    font-size: .9em;
}
